"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { useDispatch } from "react-redux";
import { toast } from "react-hot-toast";

import "../auth-common.css";
import "./login.css";

import {
  PASSWORDHIDEICON,
  PASSWORDVIEWICON,
} from "../../../../const-value/config-icons/page";

import { userLoginSchema } from "../../../../components/validation";

import {
  TITLE_USER_LOGIN,
  SUBTITLE_USER_LOGIN,
  LABEL_EMAIL,
  LABEL_PASSWORD,
  PH_PASSWORD,
  TEXT_SIGNIN,
  TEXT_SIGNUP,
  TEXT_FORGOT_PASSWORD,
  TEXT_NO_ACCOUNT,
  ROLE_USER,
  MSG_LOGIN_SUCCESS_USER,
  MSG_LOGIN_FAILED,
  PH_USER_EMAIL,
} from "../../../../const-value/config-message/page";

import { loginApi } from "../../../../lib/api/auth.api";
import { saveToken } from "../../../../lib/auth";
import { loginSuccess } from "../../../../store/authSlice";

export default function UserLoginPage() {
  const router = useRouter();
  const dispatch = useDispatch();

  const [showPass, setShowPass] = useState(false);
  const [loading, setLoading] = useState(false);

  const [form, setForm] = useState({
    email: "",
    password: "",
    type: ROLE_USER,
  });

  const onSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);

    try {
      // Yup validation
      await userLoginSchema.validate(form, { abortEarly: false });

      // API call
      const res = await loginApi(form);

      if (!res?.status || !res?.token) {
        setLoading(false);
        return toast.error(res?.message || MSG_LOGIN_FAILED);
      }

      saveToken(res.token);

      dispatch(
        loginSuccess({
          data: res.data
        })
      );

      toast.success(MSG_LOGIN_SUCCESS_USER);
      router.push("/"); 
    } catch (err) {
      if (err?.errors?.length) {
        toast.error(err.errors[0]);
      } else {
        toast.error(MSG_LOGIN_FAILED);
      }
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="auth-shell">
      {/* LEFT IMAGE */}
      <div className="auth-left d-none d-lg-flex">
        <img src="/images/auth-login.png" alt="login" />
      </div>

      {/* RIGHT FORM */}
      <div className="auth-right">
        <div className="auth-card">
          <h1 className="auth-title">{TITLE_USER_LOGIN}</h1>
          <p className="auth-sub">{SUBTITLE_USER_LOGIN}</p>

          <form onSubmit={onSubmit}>
            {/* EMAIL */}
            <label className="auth-label">{LABEL_EMAIL}</label>
            <input
              className="auth-input"
              type="email"
              placeholder={PH_USER_EMAIL}
              value={form.email}
              onChange={(e) =>
                setForm({ ...form, email: e.target.value })
              }
            />

            {/* PASSWORD */}
            <label className="auth-label">{LABEL_PASSWORD}</label>
            <div className="auth-pass-wrap">
              <input
                className="auth-input"
                type={showPass ? "text" : "password"}
                placeholder={PH_PASSWORD}
                value={form.password}
                onChange={(e) =>
                  setForm({ ...form, password: e.target.value })
                }
              />

              <span
                className="auth-pass-toggle"
                onClick={() => setShowPass(!showPass)}
              >
                {showPass ? PASSWORDVIEWICON : PASSWORDHIDEICON}
              </span>
            </div>

            {/* FORGOT */}
            <div className="login-forgot">
              <a href="/auth/forgot-password">
                {TEXT_FORGOT_PASSWORD}
              </a>
            </div>

            {/* SUBMIT */}
            <button className="auth-btn" disabled={loading}>
              {loading ? "Signing in..." : TEXT_SIGNIN}
            </button>

            {/* FOOTER */}
            <div className="auth-footer">
              {TEXT_NO_ACCOUNT}{" "}
              <a href="/auth/user/signup">{TEXT_SIGNUP}</a>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}
